import { Mark, Data } from '@ali/4ever-cangjie';

export interface HighlightData extends Data {
  value: string;
}

interface Highlight extends Mark<HighlightData> {}

const Highlight = {
  TYPE: 'highlight',

  create(highlight: string): Highlight {
    return Mark.create<HighlightData>({
      type: Highlight.TYPE,
      data: { value: highlight },
    });
  },

  isHighlight(maybeHighlight: any): maybeHighlight is Highlight {
    return Mark.isMark(maybeHighlight) && maybeHighlight.type === Highlight.TYPE;
  },
};

export default Highlight;
